<template>
    <v-container fluid>
        <!--table head-->
        <v-row 
            class="my-2 subtitle-2 secondary--text pl-12">
            Design Preferences
        </v-row>
        <v-divider />
        <v-row class="subtitle-2" no-gutters>
            <v-col cols="2"></v-col>
            <v-divider vertical></v-divider>
                    
            <v-col cols="5" class="tabCell text-center">Consumers' design type preferences</v-col>
            <v-divider vertical></v-divider>
            <v-col cols="5" class="tabCell text-center">Consumers' design quality preferences</v-col>
        </v-row>
        <v-row class="subtitle-2 text-center" no-gutters>
            <v-col class="tabCell text-left" cols="2">Segment</v-col>
            <v-divider vertical></v-divider>
            <v-col  v-for="(item, i) in types" :key="`type-${i}`"
                class="tabCell"
                cols="1">
                {{ item }}
            </v-col>
            <v-divider vertical></v-divider>
            <v-col v-for="i in 5" :key="i"
                class="tabCell"
                cols="1">
                Level {{ i }}
            </v-col>
        </v-row>
        <div v-if="market_segments.length">
            <!--segments tab-->
            <v-row v-for="segment in market_segments" :key="segment.id" no-gutters>
                <v-col cols="2" class="tabCell text-left text-truncate">{{ segment.segment.name }}</v-col>
                <v-divider vertical></v-divider>
                
                <v-col class="tabCell text-center">{{ Math.round(segment.design_classic * 100) }}%</v-col>
                <v-col class="tabCell text-center">{{ Math.round(segment.design_business * 100) }}%</v-col>
                <v-col class="tabCell text-center">{{ Math.round(segment.design_casual * 100) }}%</v-col>
                <v-col class="tabCell text-center">{{ Math.round(segment.design_innovative * 100) }}%</v-col>
                <v-col class="tabCell text-center">{{ Math.round(segment.design_art * 100) }}%</v-col>
                <v-divider vertical></v-divider>
                <v-col class="tabCell text-center">{{ Math.round(segment.design_1 * 100) }}%</v-col>
                <v-col class="tabCell text-center">{{ Math.round(segment.design_2 * 100) }}%</v-col>
                <v-col class="tabCell text-center">{{ Math.round(segment.design_3 * 100) }}%</v-col>
                <v-col class="tabCell text-center">{{ Math.round(segment.design_4 * 100) }}%</v-col>
                <v-col class="tabCell text-center">{{ Math.round(segment.design_5 * 100) }}%</v-col>
            </v-row>
        </div>
    </v-container>
</template>
<script>

export default {
    name: 'MarketDesign',
    data() {
        return {
            types: ['Classic', 'Business', 'Casual', 'Innovative', 'Art'],
        }
    },
    computed: {
        market_segments() {
            return this.$store.getters.market_segments
        },
        game() {
            return this.$store.getters.game
        }
    },
    methods: {
    },
    async mounted() {
        if (this.market_segments?.length < 1 && this.game && this.game.round > 0) {

            const params = {
                gameId: this.game.id,
                round: this.game.round - 1,
            }
            await this.$store.dispatch('fetchMarketSegments', params);
        }
    }
}
</script>