<template>
<v-container fluid class="pa-0">
	<div v-if="!loading">
		<v-row class="body-2 font-weight-bold ml-12 my-4">
			<span class="ml-4">team: {{ user.name }}</span>
			<span class="ml-4">current round: {{ game?.round }}</span>
		</v-row>

		<v-divider></v-divider>

		<v-tabs v-model="tab" height="45"
			background-color="primary"
			fixed-tabs
			slider-color="accent"
			dark>
			<v-tab :to="link(item)" ripple
				class="text-capitalize white--text"
				v-for="item in items"
				:key="item"
			>
				{{ item }}
			</v-tab>
		</v-tabs>
		<v-tabs-items v-model="tab" continuous>
			<v-tab-item value="/team/market/marketSize">
				<market-size />
			</v-tab-item>
			<v-tab-item value="/team/market/designReport">
				<design-report />
			</v-tab-item>
			<v-tab-item value="/team/market/m&mReport">
				<mm-report />
			</v-tab-item>
			<v-tab-item value="/team/market/values">
				<values />
			</v-tab-item>
			<v-tab-item value="/team/market/afReport">
				<af-report />
			</v-tab-item>
			<v-tab-item value="/team/market/media">
				<media-preferences />
			</v-tab-item>
			<v-tab-item value="/team/market/awareness&loyalty">
				<awareness-loyalty />
			</v-tab-item>
			<v-tab-item value="/team/market/products">
				<products />
			</v-tab-item>
			<v-tab-item value="/team/market/retailReports">
				<retail-reports />
			</v-tab-item>
			<v-tab-item value="/team/market/finances">
				<finances />
			</v-tab-item>
			<v-tab-item value="/team/market/hiddenNeeds">
				<hidden-needs />
			</v-tab-item>
		</v-tabs-items>
	</div>
	<v-row v-else justify="center">
		<loader />
	</v-row>
</v-container>
</template>
<script>
	import MarketSize from '../team/market/Segment.vue';
	import DesignReport from '../team/market/Design.vue';
	import MmReport from '../team/market/Materials.vue';
	import Values from '../team/market/Values.vue';
	import AfReport from '../team/market/AdvancedFeatures.vue'
	import MediaPreferences from '../team/market/MediaPreferences.vue';
	import Products from '../team/market/Products.vue';
	import RetailReports from '../team/market/RetailReports.vue';
	import Finances from '../team/market/Finances.vue';
	import HiddenNeeds from '../team/market/HiddenNeeds.vue';
	import AwarenessLoyalty from '../team/market/Awareness&Loyalty.vue';

	import Loader from '../../layouts/Loader.vue';

	export default {
		data () {
			return {
				tab: 0,
				items: [
					'Market size',
					'Design report',
					'M&M report',
					'Values', 
					'AF report',
					'Media',
					'Awareness&Loyalty', 
					'Products', 
					'Retail reports',
					'Finances', 
					'Hidden needs'
				]
			}
		},
		computed: {
			loading() {
				return this.$store.getters.loading;
			},
			user () {
				return this.$store.getters.user
			},
			game () {
				return this.$store.getters.game
			},
		},
		methods: {
			link (item) {
				const item_arr = item.split(' ')
				const first = item_arr[0].toLowerCase()
				let second = '';

				if (item_arr[1]) {
					second = item_arr[1][0].toUpperCase() + item_arr[1].substr(1)
				}
				//console.log(`/team/market/${first + second}`)				
				return `/team/market/${first + second}`
			}
		},
		components: {
			MarketSize,
			DesignReport,
			MmReport,
			Values,
			AfReport,
			MediaPreferences,
			RetailReports,
			Products,
			Finances,
			HiddenNeeds, Loader,
			AwarenessLoyalty
		},
		async mounted() {
			if (!this.game && this.user) {
				const game_id = this.user.email.match(/\d/g); //get gameId from the user email
				try {
					await this.$store.dispatch('fetchGame', game_id);
				}
				catch (error) {
					console.log(error.message)
				}
			}
		}
	}
</script>