<template>
<v-container fluid>
		<div v-if="!loading">
			<div id="print-retail-pdf">
				<!--table 1 design-->
				<v-row class="my-2 subtitle-2 secondary--text pl-12">
					Retail channels
				</v-row>

				<!--table head-->
				<v-row class="subtitle-2" no-gutters>
					<v-col v-for="(item, i) in headers"
						class="tabCell"
						:class="item.style"
						:key="i">
						{{item.text}}
					</v-col>
				</v-row>
				<v-divider />
				
				<!-- independent chains list -->
				<div v-if="chains?.length > 0">
					
					<v-expansion-panels class="mb-12"
						v-model="panel" focusable>
						<v-expansion-panel>
							<v-expansion-panel-header class="subtitle-2 secondary--text">
								Independent retail chains 
							</v-expansion-panel-header>
							<v-expansion-panel-content class="body-2">
							
								<!--Marketplaces-->
								<div v-if="marketplaces?.length">
									<div class="secondary--text my-4 text-left">
										Online marketplaces
									</div>
									<div v-for="chan in marketplaces" :key="chan.id">
										<v-row no-gutters>
											<v-col class="text-left">{{ chan.name }}</v-col>
											<v-col class="text-center">{{ chan.stores }}</v-col>
											<v-col class="text-center">{{ formatNumber(chan.peak_market_coverage) }}</v-col>
											<v-col class="text-center">{{ formatNumber(chan.peak_market_coverage * chan.stores) }}</v-col>
											<v-col class="text-center">{{ formatNumber(salesUnits(chan)) }}</v-col>
											<v-col class="text-center">{{ formatNumber(chan.entry_fee) }}</v-col>
											<v-col class="text-center">{{ formatNumber(chan.annual_fee) }}</v-col>
											<v-col class="text-center">{{ chan.retail_margin * 100 }}%</v-col>
											<v-col class="text-center">{{ chan.min_price }}</v-col>
											<v-col class="text-center">{{ chan.max_price }}</v-col>
											<v-col class="text-center">{{ chan.num_trademarks }}</v-col>
											<v-col class="text-center">{{ chan.design_main }} / {{ chan.design_second }}</v-col>
										</v-row>
										<v-divider />
									</div>
								</div>
							
								<!--Brick&mortar multibrands chains-->
								<div v-if="bm_chains?.length">
									<div class="secondary--text my-4 text-left">
										Independent brick&mortar multibrands chains
									</div>
									<div v-for="chan in bm_chains" :key="chan.id">
										<v-row no-gutters>
											<v-col class="text-left">{{ chan.name }}</v-col>
											<v-col class="text-center">{{ chan.stores }}</v-col>
											<v-col class="text-center">{{ formatNumber(chan.peak_market_coverage) }}</v-col>
											<v-col class="text-center">{{ formatNumber(chan.peak_market_coverage * chan.stores) }}</v-col>
											<v-col class="text-center">{{ formatNumber(salesUnits(chan)) }}</v-col>
											<v-col class="text-center">{{ formatNumber(chan.entry_fee) }}</v-col>
											<v-col class="text-center">{{ formatNumber(chan.annual_fee) }}</v-col>
											<v-col class="text-center">{{ chan.retail_margin * 100 }}%</v-col>
											<v-col class="text-center">{{ chan.min_price }}</v-col>
											<v-col class="text-center">{{ chan.max_price }}</v-col>
											<v-col class="text-center">{{ chan.num_trademarks }}</v-col>
											<v-col class="text-center">{{ chan.design_main }} / {{ chan.design_second }}</v-col>
										</v-row>
										<v-divider />
									</div>
								</div>
							</v-expansion-panel-content>
						</v-expansion-panel>
					</v-expansion-panels>
				</div>
				<!-- corporate channels list -->
				<div v-if="channels?.length > 0">
					<!--table head-->
					<v-divider />
					<v-row class="subtitle-2" no-gutters>
						<v-col v-for="(item, i) in headers2"
							class="tabCell"
							:class="item.style"
							:key="i">
							{{ item.text }}
						</v-col>
					</v-row>
					<v-divider />

					<v-expansion-panels class="mb-12"
							v-model="panel2" focusable>
							<v-expansion-panel>
								<v-expansion-panel-header class="subtitle-2 secondary--text">
									Corporate channels 
								</v-expansion-panel-header>
								<v-expansion-panel-content class="body-2">
							
									<!--onlineStores-->
									<div v-if="onlineStores?.length">
										<div class="secondary--text my-4 text-left">
											Corporate online retail
										</div>
										<div v-for="chan in onlineStores" :key="chan.id">
											<v-row no-gutters>
												<v-col class="text-left">{{ chan.name }}</v-col>
												<v-col class="text-center">{{ chan.stores }}</v-col>
												<v-col class="text-center">{{ formatNumber(chan.peak_market_coverage) }}</v-col>
												<v-col class="text-center">{{ formatNumber(chan.peak_market_coverage * chan.stores) }}</v-col>
												<v-col class="text-center">{{ formatNumber(salesUnits(chan)) }}</v-col>
											</v-row>
											<v-divider />
										</div>
									</div>
							
									<!--Brick&mortar corporate channels-->
									<div v-if="outlets?.length">
										<div class="secondary--text my-4 text-left">
											Corporate brick&mortar channels
										</div>
										<div v-for="chan in outlets" :key="chan.id">
											<v-row no-gutters>
												<v-col class="text-left">{{ chan.name }}</v-col>
												<v-col class="text-center">{{ chan.stores }}</v-col>
												<v-col class="text-center">{{ formatNumber(chan.peak_market_coverage) }}</v-col>
												<v-col class="text-center">{{ formatNumber(chan.peak_market_coverage * chan.stores) }}</v-col>
												<v-col class="text-center">{{ formatNumber(salesUnits(chan)) }}</v-col>
											</v-row>
											<v-divider />
										</div>
									</div>

									<!--franchaise corporate channels-->
										<div v-if="franchises?.length">
											<div class="secondary--text my-4 text-left">
												Franchise stores
											</div>
											<div v-for="chan in franchises" :key="chan.id">
												<v-row no-gutters>
													<v-col class="text-left">{{ chan.name }}</v-col>
													<v-col class="text-center">{{ chan.stores }}</v-col>
													<v-col class="text-center">{{ formatNumber(chan.peak_market_coverage) }}</v-col>
													<v-col class="text-center">{{ formatNumber(chan.peak_market_coverage * chan.stores) }}</v-col>
													<v-col class="text-center">{{ formatNumber(salesUnits(chan)) }}</v-col>
												</v-row>
												<v-divider />
											</div>
										</div>
								</v-expansion-panel-content>
							</v-expansion-panel>
					</v-expansion-panels>
				</div>
				
			</div>
		</div>
		<div v-else justify="center">
			<loader></loader>
		</div> 
	</v-container>
</template>
<script>
import Loader from '../../../layouts/Loader.vue'
import { localeNumber } from '../../../functions/common';
	export default {
		data (){
			return {
				headers: [
					{ text: 'Available channels (multibrand)', style: 'text-left'},
					{ text: 'Stores count', style: 'text-center' },
					{text: 'Peak market coverage (1 store)', style: 'text-center'}, 
					{text: 'Peak market coverage (entire chain)', style: 'text-center'}, 
					{text: 'Sales last year (units)', style: 'text-center'}, 
					{text: 'Entry fee', style: 'text-center'},
					{text: 'Annual flat fee', style: 'text-center'}, 
					{text: 'Target retail margin', style: 'text-center'}, 
					{text: 'Min preferable price', style: 'text-center'}, 
					{text: 'Max preferable price', style: 'text-center'},
					{ text: 'Number of trademarks', style: 'text-center' }, 
					{text: 'Main/second design', style: 'text-center'} 
				],
				headers2: [
					{ text: 'Available channels', style: 'text-left' },
					{ text: 'Stores count', style: 'text-center' },
					{ text: 'Peak market coverage (1 store)', style: 'text-center' },
					{ text: 'Peak market coverage (entire channel)', style: 'text-center' },
					{ text: 'Sales last year (units)', style: 'text-center' }
				],
				panel: 0,
				panel2: 0,
			}
		},		
		computed: {
			loading() {
				return this.$store.getters.loading
			},
			locale() {
				return this.$store.getters.locale;
			},
			game() {
				return this.$store.getters.game;
			},
			chains () {
				return this.$store.getters.chains;
			},
			channels() {
				return this.$store.getters.channels_all;
			},
			sales () {
				return this.$store.getters.sales;
			},
			// team channels
			onlineStores() {
				let stores = []
				if (this.channels.length > 0) {
					stores = this.channels.filter(channel => channel.type == 1)
				}
				return stores
			},
			outlets() {
				let stores = []
				if (this.channels.length > 0) {
					stores = this.channels.filter(channel => (channel.type == 2 || channel.type == 3))
				}
				return stores
			},
			franchises() {
				let stores = []
				if (this.channels.length > 0) {
					stores = this.channels.filter(channel => channel.type == 4)
				}
				return stores
			},
			marketplaces() {
				let stores = []
				if (this.chains.length > 0) {
					stores = this.chains.filter(channel => channel.type == 'online')
				}
				return stores
			},
			bm_chains() {
				let stores = []
				if (this.chains.length > 0) {
					stores = this.chains.filter(channel => channel.type == 'brick&mortar')
				}
				return stores
			},
			
		},
		methods: {
			//format in numeral
			formatNumber(number) {
				if (number) {
					return localeNumber(number, this.locale);
				}
				return 0
			},
			salesUnits(channel) {
				let sales = 0;
				this.sales.forEach((item) => {
					for (let i = 0; i< item.channels.length; i++) {
						if (item.channels[i] == channel.name) {
							sales+= item.channels_sales[i];
						}
					}
				});
				return sales;
			},
			lastSales(chan) {
				const inChain = this.channels?.filter(ch => ch.name == chan.name);
				if (inChain) {
					return inChain.reduce((sum, item) => {
						return sum += item.sales_last
					}, 0) ;
				}
				return 0;
			}
		},
		components: {Loader},
		async mounted() {
			
			if (this.chains == null && this.game) {
				try {
					//get all IndependentChains
					await this.$store.dispatch('fetchChains', this.game.id)
				}
				catch (error) {
					await this.$store.dispatch('setError', error.message);
				}
			}
			
			if (this.channels == null && this.game && this.game.round > 0) {
				try {
					//get allChannels
					await this.$store.dispatch('fetchChannels', { gameId: this.game.id, round: this.game.round - 1 })
				}
				catch (error) {
					await this.$store.dispatch('setError', error.message);
				}
			}

			if (this.sales?.length < 1 && this.game && this.game.round > 0) {
				const obj = {
					gameId: this.game.id,
					round: this.game.round - 1
				}
				try {
					//get allSales
					await this.$store.dispatch('fetchSales', obj);
				}
				catch (error) {
					await this.$store.dispatch('setError', error.message);
				}
			}
		}, 
	}	
</script>