<script>
import Loader from '../../../layouts/Loader.vue'
import { localeNumber, roundNumber } from '../../../functions/common';
export default {
    name: 'MarketFinances',
    data() {
        return {
            types: ['Classic', 'Business', 'Casual', 'Innovative', 'Art'],
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading;
        },
        locale() {
           return this.$store.getters.locale; 
        },
        game() {
            return this.$store.getters.game
        },
        finances() {
            return this.$store.getters.finances_all;
        },
        intermediary() {
            return this.$store.getters.intermediary_all;
        }
    },
    methods: {
        formatNumber(num) {
            return localeNumber(num, this.locale);
        },
        numberRound(num) {
            return roundNumber(num);
        },
        netSales(item) {
            return item.sales - item.less_promo;
        },
        netSales_procent(item) {
            const res = roundNumber((this.netSales(item) - item.CoGs) / this.netSales(item), 2);
            return res;
        },
        operatings_procent(item) {
            const res = roundNumber(((item.ga_expenses + item.rd_expenses + item.ms_expenses)) / this.netSales(item), 2);
            return res;
        },
        ebitda(item) {
            //Gross Income = netSales(item) - item.CoGs
            const res = (this.netSales(item) - item.CoGs) + item.other_incomes - (item.ga_expenses + item.rd_expenses + item.ms_expenses);
            return res;
        },
        profit(item) {
            const res = this.ebitda(item) - item.depreciation - item.interests;
            return res
        },
        change_inventories(item) {
            let res = 0
            if (this.intermediary?.length > 0) {
                const matched = this.intermediary.find((inter) => inter.teamId == item.teamId);
                if (matched) {
                    res = matched.inventories - item.inventories;
                }
            }
            return res;
        },
        net_operating_activities(item) {
            const res = roundNumber(item.less_promo + item.CoGs + item.ms_expenses + item.ga_expenses +
                + item.rd_expenses + this.profit(item) * 0.12 + item.interests + this.change_inventories(item));
            return res;
        },
        retained_profit(item) {
            const res = 
                (item.sales + 
                item.other_incomes - 
                item.CoGs - 
                item.ga_expenses - 
                item.ms_expenses - 
                item.rd_expenses - 
                item.depreciation - item.interests
                ) * 0.88;
            return res;
        },
        invest_assets(item) {
            let res = 0
            if (this.intermediary?.length > 0) {
                const matched = this.intermediary.find((inter) => inter.teamId == item.teamId);
                if (matched) {
                    res = matched.invest_assets > 0 ? matched.invest_assets : 0;
                }
            }
            return res;
        },
        invest_retail(item) {
            let res = 0
            if (this.intermediary?.length > 0) {
                const matched = this.intermediary.find((inter) => inter.teamId == item.teamId);
                if (matched) {
                    res = matched.invest_retail > 0 ? matched.invest_retail : 0;
                }
            }
            return res ;
        },
        net_cash_flow (item) {
            const res = (item.sales + item.other_incomes) - this.net_operating_activities(item) - (this.invest_retail(item) + this.invest_assets(item)) +
                +  item.borrowings;
            return res;
        },
        inventories(item) {
            let res = 0
            if (this.intermediary?.length > 0) {
                const matched = this.intermediary.find((inter) => inter.teamId == item.teamId);
                if (matched) {
                    res = matched.inventories;
                }
            }
            return res;
        },
        total_current_assets(item) {
           const res = item.cash + this.net_cash_flow(item) + this.inventories(item);
           return res;
        },
        production_assets(item) {
            let res = 0
            if (this.intermediary?.length > 0) {
                const matched = this.intermediary.find((inter) => inter.teamId == item.teamId);
                if (matched) {
                    res = matched.production_assets;
                }
            }
            return res;
        },
        retail_channels(item) {
            let res = 0
            if (this.intermediary?.length > 0) {
                const matched = this.intermediary.find((inter) => inter.teamId == item.teamId);
                if (matched) {
                    res = matched.retail_assets + matched.invest_retail - matched.retail_writte_off;
                }
            }
            return res;
        },
        net_long_term_assets (item) {
            const res = (item.accumulated_depreciation + item.depreciation) + 
                + this.retail_channels(item) + this.production_assets(item);
            return res;
        },
        assets_writte_off(item) {
           let res = 0
            if (this.intermediary?.length > 0) {
                const matched = this.intermediary.find((inter) => inter.teamId == item.teamId);
                if (matched) {
                    res = matched.inventories_writte_off + matched.retail_writte_off + matched.invest_assets;
                }
            }
            return res; 
        }  
        
    },
    async mounted() {
        if (this.finances?.length < 1 && this.game && this.game.round > 0) {

            const params = {
                gameId: this.game.id,
                round: this.game.round - 1,
            }
            await this.$store.dispatch('fetchFinances', params);
        }
        if (!this.intermediary?.length && this.game && this.game.round > 0) {

            const params = {
                gameId: this.game.id,
                round: this.game.round - 1,
            }
            await this.$store.dispatch('fetchIntermediary', params);
        }
    },
    components: { Loader }
}
</script>