<template>
    <v-container fluid>
        <div v-if="!loading">
            <v-row 
                class="my-2 subtitle-2 secondary--text pl-12">
                Values Preferences
            </v-row>
            <!--table head-->
            <v-divider />
            <v-row class="subtitle-2" no-gutters>
                <v-col v-for="(item, i) in headers" :key="i" 
                    class="tabCell"
                    :class="item.style">
                    {{ item.title }}
                </v-col>
            </v-row>
            <div v-if="market_segments.length">
                <!--segments tab-->
                <v-row v-for="segment in market_segments" :key="segment.id" no-gutters>
                    <v-col class="tabCell text-left text-truncate">{{ segment.segment.name }}</v-col>
                    <v-col class="tabCell text-center">{{ Math.round(segment.family * 100) }}%</v-col>
                    <v-col class="tabCell text-center">{{ Math.round(segment.attractiveness * 100) }}%</v-col>
                    <v-col class="tabCell text-center">{{ Math.round(segment.personality * 100) }}%</v-col>
                    <v-col class="tabCell text-center">{{ Math.round(segment.social_status * 100) }}%</v-col>
                    <v-col class="tabCell text-center">{{ Math.round(segment.fun * 100) }}%</v-col>
                    <v-col class="tabCell text-center">{{ Math.round(segment.friendship * 100) }}%</v-col>
                    <v-col class="tabCell text-center">{{ Math.round(segment.pets * 100) }}%</v-col>
                    <v-col class="tabCell text-center">{{ Math.round(segment.independent * 100) }}%</v-col>
                </v-row>
            </div>
        </div>
        <div v-else class="text-center">
            <loader></loader>
        </div>
    </v-container>
</template>
<script>
import Loader from '../../../layouts/Loader.vue'

export default {
    name: 'MarketValues',
    data() {
        return {
            headers: [
                {title: 'Segment', style: 'text-left'},
                { title: 'Family / kids', style: 'text-center' },
                { title: 'Personal attractiveness', style: 'text-center' },
                { title: 'Strong personality', style: 'text-center' },
                { title: 'Social status', style: 'text-center' },
                { title: 'Fun', style: 'text-center' },
                { title: 'Friendship', style: 'text-center' },
                { title: 'Pets', style: 'text-center' },
                { title: 'Independent style', style: 'text-center' },
            ],
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        game() {
            return this.$store.getters.game
        },
        market_segments() {
            return this.$store.getters.market_segments
        }
    },
    components: {
        Loader
    },
    async mounted() {
        if (this.market_segments?.length < 1 && this.game) {

            const params = {
                gameId: this.game.id,
                round: this.game.round,
            }
            await this.$store.dispatch('fetchMarketSegments', params);
        }
    }
}
</script>