<template>
	<v-container fluid>
		<div v-if="!loading">
			<div id="print-habits-pdf">
				<!--table -->
				<v-row class="my-2 subtitle-2 secondary--text pl-12">Products</v-row>
				
				<!--table head-->
				<v-row class="subtitle-2 mx-2" no-gutters>
					<v-col class="tabCell text-left">product</v-col>
					<v-col class="tabCell text-center" v-for="item in headers"
						:key="item">
						{{item}}
					</v-col>
				</v-row>
				<!--table content-->
				<div v-if="products?.length">
					<div v-for="item in modelsByTrademark" :key="item.trademarkId">
						<!-- trademark-->
						<div class="secondary--text text-left">
							{{ item.trademark }}
						</div>
						<div v-for="product in item.models" :key="product.id" >
							<v-row class="body-2 mx-2" no-gutters>
								<v-col class="text-left">{{ product.model }}</v-col>
								<v-col class="text-center">{{ product.retail_price }}</v-col>
								<v-col class="text-center">{{ product.design_type }}</v-col>
								<v-col class="text-center">{{ product.design }}</v-col>
								<v-col class="text-center">{{ product.material }}</v-col>
								<v-col class="text-center">{{ product.manufacturing }}</v-col>
								<v-col class="text-center">{{ product.variations }}</v-col>
								<v-col class="text-center">{{ product.advanced_feature }}</v-col>
								<v-col class="text-center">{{ formatNumber(getProductUnits(product)) }}</v-col>
							</v-row>
							<v-divider />
						</div>
						<v-row class="font-weight-bold body-2 mx-2" no-gutters>
							<v-col class="text-left">Total</v-col>
							<v-col v-for="i in 7" 
									class="text-center"
									:key="i">
							</v-col>
							<v-col class="text-center">{{ formatNumber(TotalUnits(item.models)) }}</v-col>
						</v-row>
						<v-divider />
					</div>
				</div>
				<v-row v-else justify="center" class="body-2 mt-8">
					<strong>There are no products yet</strong>
				</v-row>
			</div>
		</div>
		<v-row v-else justify="center">
			<loader />
		</v-row> 
	</v-container>
</template>
<script>
	import Loader from '../../../layouts/Loader.vue'
	import { localeNumber } from '../../../functions/common';
	export default {
		data (){
			return {
				headers: ['Retail price', 'Type of design', 'Design level', 'Material', 'Manufacturing', 'Variations', 'Advanced feature', 'Sales last year (units)'],
				tab: 0,
				
			}
		},		
		computed: {
			loading () {
				return this.$store.getters.loading;
			},
			locale() {
				return this.$store.getters.locale;
			},
			game() {
				return this.$store.getters.game;
			},
			products() {
				return this.$store.getters.products_all;
			},
			sales() {
				return this.$store.getters.sales;
			},
			modelsByTrademark() {
				const trademarks = [];
				if (this.products?.length > 0) {
					trademarks.push(
						{
							trademarkId: this.products[0].trademarkId,
							trademark: this.products[0].trademark.name,
							models: [this.products[0]]
						}
					)
					for (let i = 1; i < this.products?.length; i++) {
						const inArr = trademarks.find(tr => tr.trademarkId === this.products[i].trademarkId)
						if (inArr) {
							inArr.models.push(this.products[i]);
						}
						else {
							trademarks.push(
								{
									trademarkId: this.products[i].trademarkId,
									trademark: this.products[i].trademark.name,
									models: [this.products[i]]
								}
							)
						}
					}
				}
				return trademarks;
			},
		},
		methods: {
			formatNumber(num) {
				let res = 0;
				if (num) {
					res = localeNumber(num, this.locale);
				}
				return res;
			},
			getProductSales(product) {
				let result = 0;
				if (this.sales?.length > 0) {
					const matched = this.sales.find((item) => item.productId == product.productId);
				if (matched) {
					result = matched.sales;
				}
				}
				return result;
			},
			getProductUnits(product) {
				let result = 0;
				if (this.sales?.length > 0) {
					const matched = this.sales.find((item) => item.productId == product.productId);
					if (matched) {
						result = matched.sales_units;
					}
				}
				return result;
			},
			TotalUnits(models) {
				let res = 0;
				if (models?.length > 0) {
					res = models.reduce((sum, model) => {
						const matched = this.sales?.find((item) => item.productId == model.productId);
						if (matched) {
							return (sum+= matched.sales_units);
						}
					}, 0);
				}
				
				return res;
			},
			TotalSales(models) {
				let res = 0;
				if (models?.length > 0) {
					res = models.reduce((sum, model) => {
					const matched = this.sales?.find((item) => item.productId == model.productId);
					if (matched) {
						return (sum += matched.sales_units);
					}
				}, 0);
				}
				return res;
			}
		},
		components: { Loader },
		async mounted() {
			if (this.products?.length < 1 && this.game && this.game.round > 0) {
				const obj = {
					gameId: this.game.id,
					round: this.game.round - 1
				}
				try {
					//get allProducts
					await this.$store.dispatch('fetchProducts', obj);
				}
				catch (error) { 
					await this.$store.dispatch('setError', error.message);
				}
			}
			if (this.sales?.length < 1 && this.game && this.game.round > 0) {
				const obj = {
					gameId: this.game.id,
					round: this.game.round - 1
				}
				try {
					//get allSales
					await this.$store.dispatch('fetchSales', obj);
				}
				catch (error) {
					await this.$store.dispatch('setError', error.message);
				}
			}
		}
	}
</script>