<template>
	<v-container fluid>
		<div v-if="!loading">
			<v-row class="my-2 subtitle-2 secondary--text pl-12">
				Media costs
			</v-row>
			<!--table head-->
			<v-divider />
			<v-row class="subtitle-2" no-gutters>
				<v-col v-for="(item, i) in headers" :key="i" class="tabCell" :class="item.style">
					{{ item.title }}
				</v-col>
			</v-row>
			<div v-if="media_costs?.length">
				<!--media-costs tab-->
				<v-row v-for="cost in media_costs" :key="cost.id" no-gutters>
					<v-col class="tabCell text-center">{{ cost.smm }}</v-col>
					<v-col class="tabCell text-center">{{ cost.content_ads }}</v-col>
					<v-col class="tabCell text-center">{{ cost.bloggers_influencers }}</v-col>
					<v-col class="tabCell text-center">{{ cost.postal }}</v-col>
					<v-col class="tabCell text-center">{{ formatNumber(cost.outdoor_advertising) }}</v-col>
					<v-col class="tabCell text-center">{{ formatNumber(cost.autoradio_podcasts) }}</v-col>
					<v-col class="tabCell text-center">{{ formatNumber(cost.tv) }}</v-col>
				</v-row>
			</div>
			<v-row class="mt-8 my-2 subtitle-2 secondary--text pl-12">
				Media preferences(coverage)
			</v-row>
			<!--table head-->
			<v-divider />
			<v-row class="subtitle-2" no-gutters>
				<v-col class="tabCell text-left">
					Segment
				</v-col>
				<v-col v-for="(item, i) in headers" :key="i" 
					class="tabCell" 
					:class="item.style">
					{{ item.title }}
				</v-col>
			</v-row>
			<div v-if="media_coverage?.length">
				<!--media-costs tab-->
				<div v-for="coverage in media_coverage" :key="coverage.id">
					<v-row class="body-2" no-gutters>
						<v-col class="text-left">{{ coverage.segment.name }}</v-col>
						<v-col class="text-center">{{ Math.round(coverage.smm * 100) }}%</v-col>
						<v-col class="text-center">{{ Math.round(coverage.content_ads * 100) }}%</v-col>
						<v-col class="text-center">{{ Math.round(coverage.bloggers_influencers * 100) }}%</v-col>
						<v-col class="text-center">{{ Math.round(coverage.postal * 100) }}%</v-col>
						<v-col class="text-center">{{ Math.round(coverage.outdoor_advertising * 100) }}%</v-col>
						<v-col class="text-center">{{ Math.round(coverage.autoradio_podcasts * 100) }}%</v-col>
						<v-col class="text-center">{{ Math.round(coverage.tv * 100) }}%</v-col>
					</v-row>
					<v-divider />
				</div>
			</div>
		</div>
		<div v-else class="text-center">
			<loader></loader>
		</div> 
	</v-container>
</template>
<script>
import Loader from '../../../layouts/Loader.vue'
import { localeNumber } from '../../../functions/common';

	export default {
		data (){
			return {
				headers: [
					{ title: 'SMM (impression)', style: 'text-center' },
					{ title: 'Content Ads (impression)', style: 'text-center' },
					{ title: 'Bloggers & influencers (impression)', style: 'text-center' },
					{ title: 'Postal (mail)', style: 'text-center' },
					{ title: 'Outdoor Ads (campaign)', style: 'text-center' },
					{ title: 'Autoradio podcasts (campaign)', style: 'text-center' },
					{ title: 'TV (campaign)', style: 'text-center' },
				],
			}
		},		
		computed: {
			loading () {
				return this.$store.getters.loading
			},
			locale() {
				return this.$store.getters.locale;
			},
			game() {
				return this.$store.getters.game
			},
			media_costs() {
				return this.$store.getters.media_costs;
			},
			media_coverage() {
				return this.$store.getters.media_coverage;
			},	
		},
		methods: {
			//format in numeral
			formatNumber (param) {
				return localeNumber(param, this.locale)
			},
		},
		components: { Loader },
		async mounted() {
			
			if (this.media_costs == null && this.game && this.game.round > 0) {

				const obj = {
					gameId: this.game.id,
					round: this.game.round - 1,
				}
				try {
					await this.$store.dispatch('fetchMediaCosts', obj);
				}
				catch (error) {
					await this.$store.dispatch('setError', error.message)
				}
			}

			if (this.media_coverage == null && this.game && this.game.round > 0) {

				const obj = {
					gameId: this.game.id,
					round: this.game.round - 1
				}
				try {
					await this.$store.dispatch('fetchMediaCoverage', obj);
				}
				catch (error) {
					await this.$store.dispatch('setError', error.message)
				}
			}
		}
	}	
</script>