<template>
	<v-container fluid>
		<div v-if="!loading">
			<v-row class="my-2 subtitle-2 secondary--text pl-12">
				Trademark Awareness & Loyalty
			</v-row>
			
			<div v-if="market_segments?.length > 0">
					
				<!--table head-->
				<v-divider />
				<v-row class="subtitle-2" no-gutters>
					<v-col class="text-left" cols="2">Trademark</v-col>
					<v-divider vertical />
					<v-col class="text-center" cols="1">Awareness</v-col>
					<v-divider vertical />
					<v-col class="text-center">Loyalty by segments</v-col> 	
				</v-row>
				<v-divider />
				<v-row class="subtitle-2" no-gutters>
					<v-col class="tabCell text-left" cols="2"></v-col>
					<v-divider vertical />
					<v-col class="tabCell text-center" cols="1">(by all segments)</v-col>
					<v-divider vertical />
					<v-col v-for="seg in market_segments"
						:key="seg.id"
						class="tabCell text-center">
						{{ seg.segment?.name }}
					</v-col>	
				</v-row>
				<!--table content-->
				<div v-if="awareness?.length > 0">
					<div v-for="item in awareness" :key="item.id">
						<v-row no-gutters>
							<v-col class="tabCell text-left" cols="2">{{ item.trademark?.name }}</v-col>
							<v-divider vertical />
							<v-col class="tabCell text-center" cols="1">{{ Math.round(item.awareness * 100) }}%</v-col>
							<v-divider vertical />
							<v-col v-for="seg in market_segments"
								:key="seg.id"
								class="tabCell text-center">
								{{ Math.round(segmentLoyalty(seg.segmentId, item.trademarkId) * 100) }}%
							</v-col>
						</v-row>
					</div>
				</div>
			</div>
		</div>
		<v-row v-else justify="center">
			<loader></loader>
		</v-row> 
	</v-container>
</template>
<script>
	import Loader from '../../../layouts/Loader.vue'
	
	export default {
		data () {
			return {}
		},
		computed: {
			loading () {
				return this.$store.getters.loading;
			},
			locale() {
				return this.$store.getters.locale;
			},
			game() {
				return this.$store.getters.game;
			},
			market_segments () {
				return this.$store.getters.market_segments;
			},
			awareness() {
				return this.$store.getters.awareness;
			},
			loyalty() {
				return this.$store.getters.loyalty;
			},
			
		},
		methods: {
			segmentLoyalty(segmentId, trademarkId) {
				let res = 0;
				if (this.loyalty?.length > 0) {
					const matched = this.loyalty.find((it) => it.segmentId == segmentId && it.trademarkId == trademarkId);
					if (matched) {
						res = matched.loyalty;
					}
				}
				return res;
			}
		},
		components: {Loader},
		async mounted() {
			if (this.market_segments?.length < 1 && this.game && this.game?.round > 0) {

				const params = {
					gameId: this.game.id,
					round: this.game.round - 1,
				}
				await this.$store.dispatch('fetchMarketSegments', params);
			}
			if (this.awareness?.length < 1 && this.game && this.game?.round > 0) {

				const params = {
					gameId: this.game.id,
					round: this.game.round - 1,
				}
				await this.$store.dispatch('fetchAwareness', params);
			}
			if (this.loyalty?.length < 1 && this.game && this.game?.round > 0) {

				const params = {
					gameId: this.game.id,
					round: this.game.round - 1,
				}
				await this.$store.dispatch('fetchLoyalty', params);
			}
		}
	}
</script>